.App {
  text-align: center;
}

.Main{
  width : 40vw;
  margin: auto;
  font-family: adobe-garamond-pro;
  font-weight: 400;
  font-style: normal;
  letter-spacing: 0px;
}
h1.Main{
  font-size: 32px;
}
p.Main{
  color: rgba(26,26,26,.7);
  font-size: 18px;
  line-height: 1.6em;
  text-align: left;
}

.Menu{
  position: fixed;
  padding-left: 10vw;
  max-width: 10vw;
  text-align: left;
}
.MenuHeader{
  font-family: adobe-garamond-pro;
  font-size: 22px;
  color: #00746b;
}
.MenuButton{
  font-family: "proxima-nova","Helvetica Neue",Helvetica,Arial,sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  color: rgba(26,26,26,.4);
}
.MenuButton:hover{
  color: black;
}


